body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  margin: 0 auto;
  padding: 25px 15px;
  max-width: 1000px;
}

form {
  background-color: #bfbcbc;
  border-radius: 6px;
  padding: 20px;
  flex-wrap: wrap;
  display: flex;
  align-items: center;
  justify-content: center;
  row-gap: 10px;
}

form input[class='text'] {
  padding: 10px;
  border: 1px solid #ddd;
  outline: none;
  border-radius: 6px;
  flex: 1;
}

form input[class='submit']:focus {
  background-color: #009688 !important;
  outline: none !important;
}

form input[class='submit'] {
  border: none;
  background-color: #009688;
  color: white;
  padding: 10px;
  border-radius: 6px;
  margin-left: 10px;
  cursor: pointer;
}

.task-containere {
  cursor: pointer;
  user-select: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 0.5rem;
  transition: 0.3s;
  margin-top: 5px;
  border-radius: 6px;
}

@media (max-width:450px) {
  .task-containere {
    display: flex;
    flex-direction: column;
  }

  .task-containere .task {
    width: 90%;
  }
}

.task-containere .control {
  display: flex;
  align-items: center;
  margin-left: 1rem;
  justify-content: center;
  gap: 3px;
}

.task-containere .control span {
  font-size: 14px;
}

.task-containere .task {
  flex: 1;
  text-align: start;
  padding: 0.5rem 1rem;
  color: white;
  background-color: #009688;
  margin: 0.5rem 0px;
  transition: 0.3s;
  border-radius: 6px;
  width: 70%;
  overflow-wrap: break-word;
}

@media (max-width:450px) {
  .task-containere {
    display: flex;
    flex-direction: column;
  }

  .task-containere .task {
    width: 90%;
  }
}

.task-containere .delete,
.task-containere .edit {
  transition: 0.3s;
  flex: 0 1;
  padding: 5px;
  height: 100%;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.task-containere .delete:hover {
  background-color: red;
}

.task-containere .edit:hover {
  background-color: #009688;
}

.done {
  text-decoration: line-through !important;
  color: black !important;
  opacity: 0.5;
}

.task-done {
  opacity: 0.5;
  background-color: #eee;
}

.tasks {
  background-color: #bfbcbc;
  margin-top: 20px;
  border-radius: 6px;
  padding: 20px;
}

.clear {
  text-align: center;
}

button {
  border: none;
  transition: 0.3s;
  color: white;
  text-align: center;
  padding: 10px;
  border-radius: 6px;
  cursor: pointer;
  background-color: #c00404;
  margin: 0 auto;
}

button:hover {
  background-color: red;
}